const schema = yup.object().shape({
    tjanst: yup.string().required().typeError("Vnligen vlj tjnst!"),

    lokal: yup.string().required().typeError("Vnligen vlj lokal/objekt!"),
    yta: yup.number().required("Vnligen fyll i stdyta!").typeError("Vnligen ange lokalens yta i kvm!"),
    misc: yup.string(),

    namn: yup.string().required("Vnligen fyll i frnamn!"),
    efternamn: yup.string().required("Vnligen fyll i efternamn!"),
    adress: yup.string().required("Vnligen fyll i adress!"),
    postnummer: yup.string().required("Vnligen fyll i postnummer!").matches(/^[0-9]+$/, "Fyll i ett giltigt postnummer!").min(5, "Postnummer r fr kort!").max(5, "Postnummer r fr lngt!"),
    ort: yup.string().required("Vnligen fyll i ort!"),
    epost: yup.string().email("Vnligen fyll i en giltig e-postadress!").required("Vnligen fyll i e-postadress!"),
    telefon: yup.string().required("Vnligen fyll i telefonnummer!").phone("SE", false,"Vnligen fyll i ett giltigt telefonnummer!"),

    date: yup.string().nullable(),
    altDate: yup.string(),
    altMisc: yup.string(),
    privacy: yup.boolean().oneOf([true], "Vnligen acceptera hantering av personuppgifter!"),
});

function switchComponent(step, nextStep, prevStep, register, errors, trigger, getValues) {
    switch(step) {
        case 1:
            return (
                <Tjanst
                nextStep={ nextStep }
                register={ register }
                errors={ errors }
                trigger={ trigger } />
            )
        case 2:
            return (
                <Info 
                prevStep= { prevStep }
                nextStep={ nextStep }
                register={ register }
                errors={ errors }
                trigger={ trigger }
                values={ infoData }
                tjanst= { getValues("tjanst") } />
            )
        case 3:
            return (
                <Uppgifter
                prevStep= { prevStep }
                nextStep={ nextStep }
                register={ register }
                errors={ errors }
                trigger={ trigger } />
            )
        case 4:
            return (
                <Date
                prevStep= { prevStep }
                register={ register }
                errors={ errors } />
            )
        case 5:
            return (
                <Success />
            )
        default:
    }
}

const OffertForm = () => {
    useLayoutEffect(() => {
        window.scrollTo(0, 0)
    });

    const { register, handleSubmit, reset, trigger, formState: { errors }, getValues } = useForm({
        resolver: yupResolver(schema),
    });

    const [step, setStep] = useState(1);

    const prevStep = () => {
        setStep(step - 1);
    };
    
    const nextStep = () => {
        setStep(step + 1);
    };

    const submitForm = ( data ) => {
        nextStep();
        send(
            'service_ri402iw',
            'template_7hlgs27',
            data,
            'PsaRVewBLm0pm_EjN'
        ).then((response) => {
            reset({
                tjanst: '',

                lokal: '',
                yta: '',
                misc: '',

                namn: '',
                efternamn: '',
                adress: '',
                postnummer: '',
                ort: '',
                epost: '',
                telefon: '',

                date: '',
                altDate: '',
                altMisc: '',
                privacy: '',
            });
            console.log('SUCCESS!', response.status, response.text);
          })
          .catch((err) => {
            console.log('FAILED...', err);
          });
    };

    return (
        <div className="offert section__padding">
            <div className="offert__container">
                <div className="offert__container-progress" style={step > 4 ? {display: "none"}: {}}>
                    <div style={{background: "#3631eb"}} />
                    <div style={step > 1 ? {background: "#3631eb"}: {}} />
                    <div style={step > 2 ? {background: "#3631eb"}: {}} />
                    <div style={step > 3 ? {background: "#3631eb"}: {}} />
                </div>
                <form className="offert__container-form" onSubmit={ handleSubmit(submitForm) }>
                    {switchComponent(step, nextStep, prevStep, register, errors, trigger, getValues)}
                </form>
            </div>
        </div>
    )
        
}
export default OffertForm