import React from 'react'
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import { send } from 'emailjs-com';
import InputText from '../inputText/InputText';
import 'yup-phone';
import './contact.css';

const schema = yup.object().shape({
    namn: yup.string().required("Vnligen fyll i frnamn!"),
    email: yup.string().email("Vnligen fyll i en giltig e-postadress!").required("Vnligen fyll i e-postadress!"),
    phone: yup.string().required("Vnligen fyll i telefonnummer!").phone("SE", false,"Vnligen fyll i ett giltigt telefonnummer!"),
    subject: yup.string().test("requiredTest", "Vnligen vlj rende ur listan!",(value) => !nothingSelected(value)).required(),
    message: yup.string().required("Vnligen beskriv ditt rende!"),
    privacy: yup.boolean().oneOf([true], "Vnligen acceptera hantering av personuppgifter!"),
});

const nothingSelected = (value) => /^Ditt rende$/.test(value);

const Contact = () => {
    const { register, handleSubmit, formState: { errors }, reset } = useForm({
        resolver: yupResolver(schema),
        shouldFocusError: false,
    });

    const submitForm = ( data ) => {
        send(
            'service_ri402iw',
            'template_t0g6bpe',
            data,
            'PsaRVewBLm0pm_EjN'
        ).then((response) => {
            reset({
                namn: '',
                email: '',
                phone: '',
                subject: 'Ditt rende',
                message: '',
                privacy: '',
            });
            console.log('SUCCESS!', response.status, response.text);
          })
          .catch((err) => {
            console.log('FAILED...', err);
          });
    };
    
    return (
        <div className="contact__container">
            <form className="contact__container-form" onSubmit={handleSubmit(submitForm)}>
                    <InputText type="text" placeholder="Namn*" name="namn" register={ register } errors={ errors.namn } />
                    <InputText type="text" placeholder="E-post*" name="email" register={ register } errors={ errors.email } />
                    <InputText type="tel" placeholder="Telefonnummer*" name="phone" register={ register } errors={ errors.phone } />
                    <div className="contact__container-form_select">
                        <select name="subject" {...register("subject")} className={ errors.subject ? "contact__error" : ""} >
                            <option disabled hidden selected>Ditt rende</option>
                            <option>Vill boka en stdning</option>
                            <option>Har frgor om era fretagstjnster</option>
                            <option>Vill boka offertmte</option>
                            <option>Fakturafrga</option>
                            <option>Sker arbete</option>
                            <option>Annat</option>
                        </select>
                        <p id="under">{ errors.subject && errors.subject.message }</p>
                    </div>
                    <div id="contact__container-form_texarea">
                        <textarea name="message" placeholder="Beskriv rende" rows="10" {...register("message")} className={ errors.message ? "contact__error" : ""} />
                        <p id="under">{ errors.message && errors.message.message }</p>
                    </div>
                    <label className="contact__container-form_privacy">
                            <input type="checkbox" name="privacy" value={true} {...register("privacy")} required />
                            Jag godknner integritetspolicyn
                    </label>
                    <input className="contact__container-form_submit" value="Skicka" type="submit" name="submit" />
            </form> 
        </div>
    )
}

export default Contact