import React from 'react';
import './tjanst.css';

const Tjanst = ({ nextStep, register, errors, trigger}) => {
    const Continue = async e => {
        e.preventDefault();
        const result = await trigger("tjanst");
        if(result) nextStep();
    }

    return (
        <div className="tjanst__container">
            <h1>Begr offert</h1>
            <p>Vlj den tjnst ni r intresserad av</p>
            <div className="tjanst__container-body">
                <input type="radio" name="tjanst" value="Kontorsstdning" id="kontor" {...register("tjanst")} />
                <label htmlFor="kontor">Kontorsstdning</label>
                <input type="radio" name="tjanst" value="Trappstdning" id="trapp" {...register("tjanst")} />
                <label htmlFor="trapp">Trappstdning</label>
                <input type="radio" name="tjanst" value="Butiksstdning" id="butik" {...register("tjanst")} />
                <label htmlFor="butik">Butiksstdning</label>
                <input type="radio" name="tjanst" value="Stdning av kpcentrum" id="kpc" {...register("tjanst")} />
                <label htmlFor="kpc">Kpcenterstdning</label>
                <input type="radio" name="tjanst" value="Kontraktsstdning" id="kontrakt" {...register("tjanst")} />
                <label htmlFor="kontrakt">Kontraktsstdning</label>
                <input type="radio" name="tjanst" value="Fnsterputs" id="fnster" {...register("tjanst")} />
                <label htmlFor="fnster">Fnsterputs</label>
                <input type="radio" name="tjanst" value="Visningsstdning" id="visning" {...register("tjanst")} />
                <label htmlFor="visning">Visningsstdning</label>
                <input type="radio" name="tjanst" value="Flyttstdning" id="flytt" {...register("tjanst")} />
                <label htmlFor="flytt">Flyttstdning</label>
                <input type="radio" name="tjanst" value="Lagerstdning" id="lager" {...register("tjanst")} />
                <label htmlFor="lager">Lagerstdning</label>
            </div>
            <p id="error">{errors.tjanst && errors.tjanst.message}</p>
            <input id="nextButton" type="button" value="Nsta" onClick={ Continue } />
        </div>
    )
}

export default Tjanst