import React, { useLayoutEffect } from 'react';
import { Blog, Features, Header, WriteUs } from './containers';
import { Brand } from './components';
import header01 from './assets/header01.png';
import './App.css';

const headerData = {
  imgUrl: header01,
  title: "MZ Stdservice Nacka",
  text: "Sker du en stdfirma i Stockholm som levererar resultat? MZ Stdservice Nacka r ett stockholmsbaserat stdfretag med fokus p kvalit, noggrannhet och tillgnglighet.",
  button1: {
    text: "Boka stdning",
    reff: "/Offertforfragan",
  },
  button2: {
    text: "Ls mer om oss",
    reff: "/Om-Stdservice-nacka",
  },
} 

const App = () => {
  useLayoutEffect(() => {
    window.scrollTo(0, 0)
});

  return (
    <div className="App" id="outer-container">
        <Header prop={ headerData } />
        <Brand />
        <Blog />
        <Features />
        <WriteUs />
    </div>
  )
}

export default App