import React from 'react';
import { Article } from '../../components';
import { blog00, blog01, blog02, blog05 } from './imports';
import './blog.css';
import { Link } from 'react-router-dom';

const Blog = () => {
  return (
      <div className="gpt3__blog-container section__margin" id="blog">
        <div className="gpt3__blog-container_groupA">
          <h3>Vra Tjnster</h3>
          <p>Vra tjnster r specialanpassade fr fretag och freningar i Stockholm som sker regelbunden fretagsstdning. Vi erbjuder tjnster i form av butiksstdning, kontorsstdning och trappstdning. Klicka p lnken nedan fr vidare information.</p>
          <div className="gpt3__blog-container_groupA-links">
            <Link to="Offertforfragan" className="blog__hover-underline-animation" >Offertfrfrgan</Link>
            <Link to="Tjanster" className="blog__hover-underline-animation" >Se alla vra tjnster</Link>
          </div>
        </div>
        <div className="gpt3__blog-container_groupB">
          <Link to="Tjanster/0"><Article imgUrl={blog00} title="Kontorsstdning" /></Link>
          <Link to="Tjanster/1"><Article imgUrl={blog01} title="Trappstdning" /></Link>
          <Link to="Tjanster/2"><Article imgUrl={blog02} title="Butiksstdning" /></Link>
          <Link to="Tjanster/5"><Article imgUrl={blog05} title="Fnsterputs" /></Link>
        </div>
      </div>
  )
}

export default Blog