import React from 'react';
import { InputText } from '../../../components';
import './info.css';

const Info = ({ prevStep, nextStep, register, errors, trigger, values, tjanst }) => {
    const Continue = async e => {
        e.preventDefault();
        const result = await trigger(["lokal", "yta"]);
        if(result) nextStep();
    }

    const Back = e => {
        e.preventDefault();
        prevStep();
    }

    const picked = values.find(obj => obj.id === tjanst);
    return (
        <div className="info__container">
            <h1>Information om stdningen</h1>
            <p>Vlj lokal/objekt</p>
            <div className="info__container-radio">
                {picked.buttons.map((item, index) =>(
                    <React.Fragment key={item.id}>
                        <input type="radio" name="lokal" value={item.value} id={item.id} {...register("lokal")} />
                        <label htmlFor={item.id}>{item.value}</label>
                    </React.Fragment>   
                ))}
                <input type="radio" name="lokal" value="Annat" id="annat" />
                <label htmlFor="annat">Annat</label>
            </div>
            <p id="error">{ errors.lokal && errors.lokal.message }</p>
            <div className="info__container-text">
                    <InputText type="text" label="Hur stor r stdytan?*" name="yta" placeholder="Stdyta kvm" size={35} register={ register } errors={ errors.yta } />
                    <p>Har du ngra andra ev. nskeml?</p>
                    <textarea name="misc" placeholder="Andra nskeml?" id="misc" rows="10" {...register("misc")} />
            </div>
            <div>
                <input id="prevButton" type="button" value="Tillbaka" onClick={ Back } />
                <input id="nextButton" type="button" value="Nsta" onClick={ Continue } />   
            </div>
        </div>
    )
}

export default Info