import React from 'react'
import { Feature } from '../../components';
import './features.css';

const featuresData = [
  {
    title: 'Vr affrsid',
    text: 'Att utfra stdtjnster till ett konkurrensmssigt pris med huvudfokus p hg servicestandard och 100% kundnjdhet.'
  },
  {
    title: 'Vr vision',
    text: 'Vr vision r att vara ett etablerat och nyskapande fretag som alltid r det sjlvklara valet fr kunden. Kvalitet, noggrannhet och tillgnglighet r tre ledord som ska genomsyra hela fretaget.'
  },
  {
    title: 'Vra vrderingar',
    text: <span>
        Vi p MZ Stdservice Nacka r ett fretag som vrdestter kundnjdhet och en sund arbetsmilj fr vra anstllda.
        <br/><br/>
       Vra frmsta vrderingar r:
        <ul>
          <li>Kvalitet:En grundpelare i vr affrsid r att alltid leverera kvalit</li>
          <li>Noggrannhet:De sm detaljerna r minst lika viktiga</li>
          <li>Tillgnglighet:Vi r tillgngliga nr kunden behver oss</li>
        </ul>
    </span>,
  },
]

const Features = () => {
  return (
    <div className="gpt3__features section__margin" id="features">
        <div className="gpt3__features-image" />
        <div className="gpt3__features-container">
          <h1 className="gpt3__features-container_title">MZ Stdservice Nacka  Det sjlvklara valet i Stockholm</h1>
          {featuresData.map((item, index) =>(
            <Feature title={item.title} text={item.text} key={item.title + index} />
          ))}
        </div>
    </div>
  )
}

export default Features